@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body{font-family:Inter;color:rgba(0,0,0,0.85);background:#ebeef3;margin:0;padding:0;box-sizing:border-box !important}*{margin:0;padding:0}label{cursor:pointer}button{background-color:inherit;border:none;cursor:pointer}button:focus{outline:none;border:none}button:active{outline:none;border:none}a{text-decoration:none;color:#000;color:initial}h1,h2,h3,h4,h5{font-weight:normal}

.article-container{box-sizing:border-box !important;background-color:#fff;width:100%;display:flex;flex-direction:column;justify-content:space-between;padding:10px 15px;border-radius:5px;-webkit-filter:drop-shadow(0px 4px 12px rgba(0,0,0,0.15));filter:drop-shadow(0px 4px 12px rgba(0,0,0,0.15));margin-bottom:20px;max-width:960px;margin:auto;margin-top:25px}.article{box-sizing:border-box !important;background-color:#fff;width:100%;display:flex;justify-content:space-between;padding:10px 15px;border-radius:5px;-webkit-filter:drop-shadow(0px 4px 12px rgba(0,0,0,0.15));filter:drop-shadow(0px 4px 12px rgba(0,0,0,0.15));margin-bottom:20px}.article__delete-btn{color:#f5222d;border:1px solid #f5222d;box-sizing:border-box;border-radius:5px;padding:6px 17px;font-size:14px;line-height:22px}.article__delete-btn:focus{color:white;background-color:#f5222d;transition:.3s ease-in-out;border:1px solid #f5222d}.article__delete-btn:hover{color:white;background-color:#f5222d;transition:.3s ease-in-out}.article__delete-btn--disable{display:none}.article__edit-btn{color:#52c41a;border:1px solid #52c41a;box-sizing:border-box;border-radius:5px;padding:6px 17px;font-size:14px;line-height:22px;margin-right:10px}.article__edit-btn:focus{color:white;background-color:#52c41a;transition:.3s ease-in-out;border:1px solid #52c41a}.article__edit-btn:hover{color:white;background-color:#52c41a;transition:.3s ease-in-out}.article__edit-btn--disable{display:none}.article__header{display:flex;align-items:center}.article__like{margin-left:10px;display:flex;align-items:bottom;font-size:17px;cursor:pointer}.article__like--active{color:red}.article__like-counter{display:flex;margin-left:5px;font-size:12px}.article__full{max-width:960px;margin:auto;display:flex;justify-content:space-between;margin-top:25px;width:100%}.article__content{display:flex;flex-direction:column}.article__title{color:#1890ff !important;font-size:20px;line-height:28px;margin-bottom:4px}.article__tags{display:flex;margin-bottom:7px}.article__tag{font-size:12px;cursor:pointer;padding:3px 5px;margin-right:10px;display:flex;color:rgba(0,0,0,0.5);border-radius:3px;border:1px solid rgba(0,0,0,0.5)}.article__tag--active{padding:3px 5px;display:flex;color:rgba(0,0,0,0.8);border-radius:3px;border:1px solid rgba(0,0,0,0.8)}.article__article{font-size:12px;line-height:22px;max-width:680px;margin-bottom:25px}.article__article-full{font-size:12px;line-height:22px;max-width:100%;margin-bottom:25px}.article__author-block{min-width:150px;display:flex;flex-direction:row;cursor:pointer}.article__author-description{display:flex;flex-direction:column;margin-right:10px}.article__autor{font-size:18px;color:rgba(0,0,0,0.85)}.article__date{color:rgba(0,0,0,0.5);font-size:12px;line-height:28px}.article__image{max-width:50px}.article__body{font-size:20px;line-height:28px;color:rgba(0,0,0,0.9);margin-bottom:25px}

.header{display:flex;justify-content:space-between;align-items:center;height:80px;padding:0 20px;background-color:#fff}.header__autor{display:flex;align-items:center}.header__buttons{display:flex;justify-content:space-between;align-items:center}.header__button{margin-left:20px}.header__logo{font-size:18px}.header-button--sign-up{color:#52c41a;border:1px solid #52c41a;border-radius:5px;padding:5px 18px 10px 18px}.header-button--sign-up:hover{background-color:#52c41a;color:white;transition:all .3s ease-in-out}.header-button--sign-in:hover{color:#52c41a;opacity:.9;transition:all .3s ease-in-out}.header-button--create-article{padding:6px 10px;border:1px solid #52c41a;box-sizing:border-box;border-radius:5px;color:#52c41a;margin-right:20px;line-height:22px;font-size:14px}.header-button--create-article:hover{background:#52c41a;color:white;transition:.3s ease-in-out}.header-button--logout{font-size:18px;line-height:28px;padding:6px 18px 10px 18px;border:1px solid rgba(0,0,0,0.75);box-sizing:border-box;border-radius:5px;color:rgba(0,0,0,0.75);margin-left:20px}.header-button--logout:hover{background:rgba(0,0,0,0.75);color:white;transition:.3s ease-in-out}

.articles{display:flex;justify-content:center;flex-direction:column;max-width:940px;padding:15px;margin:auto;box-sizing:border-box !important}.paginator{display:flex;margin:auto}

h1,h2,h3,h4,h5{margin:0;padding:0}.sign_up{display:flex;margin:auto;max-width:380px;background-color:#fff;border:1px solid #d9d9d9;box-sizing:border-box;box-shadow:0px 22px 106px rgba(0,0,0,0.07),0px 9.19107px 44.2843px rgba(0,0,0,0.0503198),0px 4.91399px 23.6765px rgba(0,0,0,0.0417275),0px 2.75474px 13.2728px rgba(0,0,0,0.035),0px 1.46302px 7.04911px rgba(0,0,0,0.0282725),0px 0.6088px 2.93329px rgba(0,0,0,0.0196802);border-radius:6px;margin-top:60px}.form{padding:0 20px;display:flex;flex-direction:column;width:100%}.form__spin{display:flex;margin:auto;justify-content:center;margin-top:40px}.form__policity-label{color:rgba(0,0,0,0.65)}.form__policity-label--error{color:red}.form__policity:hover .form__policity-label::before{border:1px solid #1890ff}.form__policity:hover .form__policity-input:checked+label::before{content:'';display:inline-block;width:16px;height:16px;border:0px solid #1890ff;border-radius:2px;border-collapse:separate;transition:all 0.3s;margin-right:7px}.form__policity-input{position:absolute;z-index:-1;opacity:0}.form__policity-input+label{display:inline-block;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.form__policity-input+label::before{content:'';display:inline-block;width:16px;height:16px;border:1px solid #d9d9d9;border-radius:2px;border-collapse:separate;transition:all 0.3s;margin-right:7px;background-repeat:no-repeat;background-position:center center;background-size:100% 100%}.form__policity-input:checked+label::before{content:'';display:inline-block;width:16px;height:16px;border:0px solid #d9d9d9;border-radius:2px;border-collapse:separate;transition:all 0.3s;margin-right:7px;background-image:url(/static/media/no-label.411e58c6.svg)}.form__header-container{width:100%;display:flex;justify-content:center;margin-top:50px;margin-bottom:20px}.form__header{color:#262626;font-weight:500;font-size:20px;line-height:28px;font-family:Roboto;display:flex}.form__input-group{display:flex;margin-bottom:12px;flex-direction:column}.form__input-label{color:#262626;font-family:Roboto;font-weight:normal;font-size:14px;line-height:24px}.form__input-label--error{color:#f5222d}.form__input{border:1px solid #d9d9d9;box-sizing:border-box;border-radius:4px;padding:8px 12px;color:rgba(0,0,0,0.75);font-size:16px;line-height:24px;font-family:Roboto;font-style:normal;font-weight:normal}.form__input:focus{border:1px solid #1890ff;outline:none}.form__input--error{border:1px solid #f5222d}.form__input--error:focus{border:1px solid #f5222d;outline:none}.form__divider{width:100%;margin:8px 0;border-bottom:1px solid #e8e8e8}.form__policity{margin-bottom:20px}.form__submit{width:100%;background-color:#1890ff;padding:8px 16px;border-radius:4px;color:white;margin-bottom:8px;font-size:16px;line-height:24px;font-family:Roboto}.form__link{color:#1890ff}.form__container{display:flex;justify-content:center;margin-bottom:50px}.form__tag{margin-bottom:5px}.form__tag input{margin-right:15px;width:300px}.form__delet-tag-btn{padding:8px 16px;border:1px solid #f5222d;color:#f5222d;border-radius:4px}.form__delet-tag-btn:hover{background-color:#f5222d;color:white;transition:.3s ease-in-out}.form__add-tag-btn{padding:8px 16px;border:1px solid #1890ff;color:#1890ff;border-radius:4px}.form__add-tag-btn:hover{background-color:#1890ff;color:white;transition:.3s ease-in-out}.disable{display:none;font-family:Roboto;font-style:normal;font-weight:normal;font-size:14px;line-height:22px}.mg-top--10px{margin-top:10px}.mg-bottom--40px{margin-bottom:40px}.mg-right--15px{margin-right:15px}.new-article{max-width:940px;display:flex;margin:auto;background-color:#fff;border:1px solid #d9d9d9;box-sizing:border-box;box-shadow:0px 22px 106px rgba(0,0,0,0.07),0px 9.19107px 44.2843px rgba(0,0,0,0.0503198),0px 4.91399px 23.6765px rgba(0,0,0,0.0417275),0px 2.75474px 13.2728px rgba(0,0,0,0.035),0px 1.46302px 7.04911px rgba(0,0,0,0.0282725),0px 0.6088px 2.93329px rgba(0,0,0,0.0196802);border-radius:6px;margin-top:60px}.new-article__send-btn{max-width:320px;margin-right:auto}

